import styled from "styled-components";

const Input = styled.input`
  padding: 0 25px;
  width: 100%;
  height: 80px;
  font-size: 1.8em;
  border: 0;
  border-radius: 5px;
  background-color: rgb(37, 51, 65);
  color: #fff;

  @media (max-width: 768px) {
    padding: 0 10px;
    height: 50px;
    font-size: 1em;
  }
`;

export default Input;



